<script>
	export default {
		name:'SolarCalender',
		data(){
			return {
				weekList: ['一','二','三','四','五','六','七'],
				today: new Date().getDate(),
				bigMonthList: [1,3,5,7,8,10,12]
			}
		},
		props:{
			year:{
				type:Number,
				default(){
					return new Date().getFullYear();
				}
			},
			month:{
				type:Number,
				default(){
					return new Date().getMonth() + 1;
				}
			},
			selectDays:{
				type:Array,
				default(){
					return [13];
				}
			},
			clickDayCallBack:{
				type: Function
			}
		},
		computed: {
			dateList:function(){
				return this.initDateList(this.year , this.month);
			}
		},
		methods:{
			initDateList(year , month){
				const maxDate = this.getDaysOfMonth(month , this.isNonlea(year));
				const dateList = [];
				for(let i = 1;i <= 5;i++){
					dateList[i-1] = [];
					for(let j = (i - 1) * 7;j < i * 7 && j < maxDate;j++){
						dateList[i-1].push(j + 1);
					}	
				}
				return dateList;
			},
			isNonlea(year){ 
				let result = true;
				if ((year % 4 === 0 && year % 100 !== 0) || (year % 400===0)) {
					result = false;
				}
				return result;
			},
			getDaysOfMonth(month,isNonlea){
				let day = 30;
				if (this.bigMonthList.indexOf(month) >-1) {
					day = 31;
				}
				else if (month === 2) {
					if (isNonlea) {
						day = 28;
					}
					else{
						day = 29;
					}
				}
				return day;
			},
			clickday(ev){
				const target = ev.target;
				if (target.innerText) {
					this.today = target.innerText;
					this.clickDayCallBack && this.clickDayCallBack({year:this.year , month:this.month, day: this.today});
				}
			}
		}
	}
</script>
<template>
	<div class="solar-calender">
		<table>
			<thead>
				<tr>
					<th v-for="value in weekList">{{value}}</th>
				</tr>
			</thead>
			<tbody @click="clickday($event)">
				<tr v-for="lineItem in dateList">
					<td v-for="dateValue in lineItem">
						<span :class="today == dateValue ? 'today' : ''">{{dateValue}}</span>
						<span class="selectDay" v-if="selectDays.indexOf(dateValue) > -1"></span>
					</td>
				</tr>
			</tbody>
		</table>
	</div>
</template>
<style lang='less' scoped>
	@import '../../assets/css/solarCalender.less';
</style>


