<script>
import Header from './common/Header.vue'
import SolarCalender from './common/SolarCalender.vue'
import ExaminationItem from './common/ExaminationItem.vue'
import {mapGetters , mapActions} from 'vuex'

export default {
  name:'Schedule',
  components:{
     'common-header': Header,
     'solar-calender': SolarCalender,
     'examination-item': ExaminationItem,
  },
  data () {
    return {
      msg: 'Welcome to Your Vue.js App',
      titlename: '日程',
      year: 2017,
      month: 11,
      selectDays:[12,13],
      pageIndex: 1,
      pageSize:100
    }
  },
  created(){
    const date = new Date();
    this.year = date.getFullYear();
    this.month = date.getMonth() + 1;
    this.getExamDate({month: this.month , year: this.year});
    this.getExamRoomList({date: year + '-' + month ,pageIndex: this.pageIndex ,pageSize: this.pageSize});
  },
  computed:{
    ...mapGetters(['examDate' , 'examRoomList'])
  },
  methods:{
    ...mapActions(['getExamDate' , 'getExamRoomList']),
    selectMonth(){
      const dtPicker = new mui.DtPicker({
        type: 'month',
        beginDate: new Date(1967, 1,1),//设置开始日期
        endDate: new Date(2026, 12,31),//设置结束日期
        value: this.year + '-' + this.month
      });
      dtPicker.show((selectItems)=>{
        if (selectItems) {
          const value = selectItems.value;
          this.year = value.split('-')[0] - 0;
          this.month = value.split('-')[1] - 0;
          this.getExamDate({month: this.month , year: this.year});
        }
      });
    },
    clickDayCallBack(date){
      this.getExamRoomList({date ,pageIndex: this.pageIndex ,pageSize: this.pageSize});
    }
  }
}
</script>
<template>
    <div class="schedule">
      <common-header :title="titlename"></common-header> 
      <div class="schedule-condition">
        <div @click="selectMonth" class="choose-date">
          {{year}} 年 {{month}} 月 
        </div>
        <solar-calender :year="year" :month="month" :selectDays="examDate" :clickDayCallBack="clickDayCallBack"/>
      </div>
      <ul class="examination-list">
          <examination-item  v-for="item in examRoomList" :examinfo = "item"/>
      </ul>
    </div>
</template>
<style lang="less" scoped>
 @import "../assets/css/schedule.less";
</style>


